.nav {
    display: flex;
    align-items: center;
    justify-content: space-between;
    overflow-x: auto;
    overflow-y: hidden;
    margin-left: 50px;
    margin-right: 30px;
    flex-grow: 1;

    &__list {
        display: flex;
        align-items: center;

        &:not(:first-child) {
            margin-left: 32px;
        }

        @media (max-width:$tablet-max) {
            flex-direction: column;

            &:not(:first-child) {
                margin-left: 0px;
                margin-top: 45px;
            }
        }
    }

    &__item {
        &:not(:last-child) {
            margin-right: 32px;

            @media (max-width:$tablet-max) {
                margin-right: 0;
                margin-bottom: 45px;
            }
        }
    }

    &__link {
        transition: color $transitionSpeed $transitionFunction;
        color: var(--dark);
        white-space: nowrap;
        font-size: 16px;
        line-height: math.div(20, 16);
        font-weight: 500;

        @media (min-width:$smallTablet-min) {
            &:hover {
                color: var(--blue);
            }
        }

        @media (max-width:$smallTablet-max) {
            font-size: 14px;
        }
    }

    @media (max-width:$tablet-max) {
        visibility: hidden;
        transform: translateX(100%);
        transition: all $transitionSpeed $transitionFunction;

        justify-content: start;
        flex-direction: column;

        padding: 50px 0 20px;

        position: absolute;
        top: 100%;
        left: -16px;
        z-index: var(--index-4);
        width: calc(100% + 32px);
        height: 100%;
        min-height: calc(var(--vh) - var(--header-height));
        margin: 0;
        background-color: var(--white);

        &._active {
            box-shadow: 0px -2px 1px rgba(0, 0, 0, 0.1);
            visibility: visible;
            transform: translateX(0);
        }
    }

    @media (max-width:$smallTablet-max) {
        justify-content: start;
    }
}
